.todoLi{
  margin: 10px 20px 10px 10px;
  border-right: 10px;
  list-style-type: circle;
  color: #393d3f;
  font-size: 18px;
  font-weight: 300px;
}

.liButton{
  display: inline-block;
  border:5px;
  margin-left: 0.7%;
  padding:0px;
  width: 40px;
  height: 30px;
  border-radius: 5px;
  background-color: #ff8fa3;
  color: whitesmoke;
  border-color: whitesmoke;
}

.delButton{
  display: inline-block;
  border:5px;
  margin-left: 0.6%;
  padding:0px;
  width: 30px;
  height:30px;
  border-radius: 5px;
  background-color: gray;
  color: whitesmoke;
}
